html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  margin-left: 50px;
  display: flex;
}

.catalog {
  perspective: 400px;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.catalog__card {
  transition: transform .3s;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  padding: 0;
  list-style: none;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__feedback {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  padding: 0;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  padding: 0;
  font-size: 12px;
}

.card__price-cost {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  transition: color, background-color .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 :last-child {
  background-image: url("star.822d561e.svg");
}
