.stars {
  display: flex;
  gap: 4px;

  &__star {
    background-image: url(../images/star-active.svg);
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    background-position: center;
  }

  &--4 {
    :last-child {
      background-image: url(../images/star.svg);
    }
  }
}
